
































































































.card-content {
	position: relative;
	width: 100%;
	padding-bottom: 52px;
	overflow: hidden;
	word-break: break-all;
	word-break: break-word;
	.md-subhead {
		.md-icon {
			$size: 16px;

			width: $size;
			min-width: $size;
			height: $size;
			min-height: $size;
			font-size: $size;
			line-height: $size;
		}

		span {
			vertical-align: middle;
		}
	}
	.md-card-actions {
		position: absolute;
		right: 0;
		bottom: 0;
		left: 0;
		.provider-name {
			position: absolute;
			left: 16px;
		}
	}
}
