





















































































































































































































































































































































.container-fluid {
	max-width: 1000px;
	padding: 30px 16px;
}
.gutter > div {
	padding: 5px;
}

.md-card {
	width: 100%;

	h3 {
		margin: 2.5em 0 0.5em 0;
		&:first-of-type {
			margin-top: 0;
		}
	}
}

.preview-wrapper {
	display: block;
	width: 100%;
}

textarea {
	min-height: 12rem !important;
}

.md-card-actions {
	.delete {
		position: absolute;
		left: 8px;
	}
}
