
















































.wrapper {
	margin: 1.5em 0;
}
label {
	padding-left: 8px;
	font-size: 0.9em;
	font-weight: 500;
	color: black;
}
.input {
	display: block;
	width: 100%;
	padding: 0 8px 8px;
	font-size: 1.15em;
	color: #757575;
	border: none;
	border-bottom: 1px solid #949494;

	&:focus {
		border-bottom-color: #b10438;
		border-bottom-width: 2px;
		outline: none;
	}
}
span {
	font-size: 0.75em;
}
.invalid {
	color: #b10438;
}
